$orange: #ef571b;
body {
    line-height: 1.5;
    color: #333333;
}
h1 {
    text-align: center;
    margin: 0px 0px 10px;
}
h2 {
    text-align: center;
    margin: 0px 0px 15px;
    font-family: serif;
    font-style: italic;
    font-size: 1.7em;
}
.microcosm  {
}
.cover {
    width: 100%;
    height: 100%;
    background: url(/img/microcosm_kv.jpg) no-repeat center;
    background-size: cover;
}
.cover-inner {
    position: relative;
    text-align: center;
    color: #fff;
    background: rgba(0, 0, 0, 0.2);
    height: 100%;
}
.cover-text {
    position: relative;
    top: 63%;
    font-size: 12px;
    font-family: serif;
}
.catchcopy {
    font-size: 16px;
}
.abstract {
    text-align: center;
    line-height: 3.5;
}
.btn-wrap {
    margin: 12px;
}
.btn {
    width: 200px;
    margin: 10px;
    padding: 8px;
    color: #f05279;
    border: 2px solid #f05279;
    text-decoration: none;
    transition: background-color 0.2s ease-out, color 0.2s ease-out;
}
.btn:hover {
    background: #f05279;
    color: #fff;
    transition: background-color 0.3s ease-out, color 0.3s ease-out;
}

.item {
    padding: 12px;
    box-sizing: border-box;
}
.item-inner {
    border: 2px solid $orange;
    padding: 8px;
    min-height: 12em;
}
.item-inner .head {
    text-align: center;
    font-weight: bold;
    border-bottom: 1px solid #ef8e1b;
}
.item-inner .body {
    margin-top: 1em;
}
.icon {
    font-size:3em;
    color: #f05279;
}
.imgs {
    width: 100%;
    text-align: center;
}
.center {
    text-align: center;
}
.section {
    padding-top: 20px;
    padding-bottom: 30px;
}
.color {
    background: #f2f2f2;
}
.color2 {
    background: #f8f8f8;
}
.section-inner {
    width: 100%;
    margin: 0 auto;
}
.image-border {
    padding: 5px;
    box-sizing: border-box;
}
.image-border-inner {
    border: 3px solid #ccc;
    min-height: 170px;
    padding-top: 15px;
}
.image-border img {
    width: 100%;
    max-width: 200px;
}
dt {
    font-weight: bold;
}
dt.lineup {
    margin-top: 4px;
    border-bottom: 1px dotted $orange;
}
.dl {
    border: 2px solid $orange;
    padding: 12px;
    margin-bottom: 1em;
}
.dl h3 {
    margin-top: 0px;
    text-decoration: underline;
}
.dl-link {
    text-align: right;
}
.faq {
    margin: 8px 0px 4px;
}
.footer {
    padding: 15px 0px;
    text-align: center;
}

#page-top {
    position: fixed;
    bottom: 14px;
    right: 20px;
    z-index: 10;
}
#page-top a {
    text-decoration: none;
    width: 50px;
    padding: 4px 0;
    color: #f05279;
    border: 2px solid #f05279;
    font-size: 1.4em;
    text-align: center;
    display: block;
    transition: background-color 0.2s ease-out, color 0.2s ease-out;
}
#page-top a:hover {
    text-decoration: none;
    background: #f05279;
    color: #fff;
    transition: background-color 0.3s ease-out, color 0.3s ease-out;
}

table.dataTable thead th, table.dataTable thead td {
    border-bottom: 1px solid $orange;
}
table.dataTable.no-footer {
    border-bottom: 1px solid $orange;
}

@media(min-width: 540px) {
    br.sp {
        display: none;
    }
    .cover-text {
        position: relative;
        top: 63%;
        font-size: 28px;
        font-family: serif;
    }
    .section-inner {
        width: 100%;
        margin: 0 auto;
        padding-left: 10px;
        padding-right: 10px;
        box-sizing: border-box;
    }
}
@media(min-width: 840px) {
    .section-inner {
        width: 800px;
        margin: 0 auto;
        padding-left: 10px;
        padding-right: 10px;
        box-sizing: border-box;
    }
}
